import React from "react";
import { connect } from "react-redux";
import { getCart, ClearCart } from "../../service/thunk/thunkCreator";
import { OPEN_MODAL } from "../../service/actions/actions";
import { useEffect } from "react";
import "./style.css";
import CartItem from "./cart-item";

const Cart = ({
  cart,
  getCart,
  isAdded,
  isUpdate,
  total,
  OPEN_MODAL,
  ClearCart,
}) => {
  useEffect(() => {
    getCart();
  }, []);

  useEffect(() => {
    getCart();
  }, [isAdded, isUpdate]);

  return (
    <React.Fragment>
      <div className="cart">
        <div className="cart-header">
          <h3>Мой заказ</h3>
          <svg
            width="1em"
            height="1em"
            viewBox="0 0 16 16"
            className="bi bi-trash-fill trash-icon"
            fill="currentColor"
            xmlns="http://www.w3.org/2000/svg"
            onClick={() => ClearCart()}
          >
            <path
              fillRule="evenodd"
              d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5a.5.5 0 0 0-1 0v7a.5.5 0 0 0 1 0v-7z"
            />
          </svg>
        </div>
        <div className="cart-body list-group">
          {isUpdate ? (
            <div className="spinner">
              <div className="spinner-border text-dark"></div>
              <span>Обновляем вашу корзину</span>
            </div>
          ) : (
            cart.map((item, index) => <CartItem item={item} key={index} />)
          )}
        </div>
        <div className="cart-footer">
          <button
            className="btn btn-warning btn-lg"
            onClick={() => OPEN_MODAL()}
          >
            Оформить заказ
          </button>
          <div className="total-price-wrapper">
            <h3 className="cart-total-price">Итого:{total}</h3>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    cart: state.CartReducer.cart,
    total: state.CartReducer.total,
    isAdded: state.CartReducer.isAdded,
    isUpdate: state.CartReducer.isUpdate,
    isModal: state.CartReducer.isModal,
  };
};

export default connect(mapStateToProps, { getCart, OPEN_MODAL, ClearCart })(
  Cart
);
