import axios from "axios";
function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== "") {
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      // Does this cookie string begin with the name we want?
      if (cookie.substring(0, name.length + 1) === name + "=") {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}

export default class ApiManager {
  api = "https://my-first-pr.herokuapp.com/api/";
  standart_config = {
    "Content-Type": "application/json",
    Accept: "application/json",
    credentials: "include",
  };

  async getResource(url, config = this.standart_config) {
    const raw_data = await axios
      .get(`${this.api}${url}`, config)
      .catch((error) => console.log(error.response));
    return raw_data;
  }

  async getProductList(filter, nameFilter) {
    const product_list = await this.getResource(
      `products?${filter}&${nameFilter}`
    );

    return product_list;
  }

  async getCategorylist() {
    const category_list = await this.getResource("products/type");
    return category_list;
  }

  async getCart() {
    const result = await axios.get(
      "https://my-first-pr.herokuapp.com/api/cart/",
      {
        withCredentials: true,
      }
    );
    console.log(result);
    return result;
  }

  async clearCart() {
    const csrftoken = getCookie("csrftoken");
    const result = await axios.delete(
      "https://my-first-pr.herokuapp.com/api/cart/",
      {
        withCredentials: true,
        headers: { "X-CSRFToken": csrftoken },
      }
    );

    return result;
  }

  async postItemToCart(id) {
    const csrftoken = getCookie("csrftoken");
    const result = await axios.put(
      "https://my-first-pr.herokuapp.com/api/cart/",
      { id: id, remove: 0, quantity: 1, update_quantity: 0 },
      { withCredentials: true, headers: { "X-CSRFToken": csrftoken } }
    );

    return result;
  }

  async UpdateItemInCart(id, quantity) {
    const csrftoken = getCookie("csrftoken");
    const result = await axios.put(
      "https://my-first-pr.herokuapp.com/api/cart/",
      { id: id, remove: 0, quantity: quantity, update_quantity: 1 },
      { withCredentials: true, headers: { "X-CSRFToken": csrftoken } }
    );
    return result;
  }

  async DeleteItemInCart(id) {
    const csrftoken = getCookie("csrftoken");
    const result = await axios.put(
      "https://my-first-pr.herokuapp.com/api/cart/",
      { id: id, remove: 1 },
      { withCredentials: true, headers: { "X-CSRFToken": csrftoken } }
    );
    return result;
  }

  async postCart(address, firstName, lastName, phone, email) {
    const csrftoken = getCookie("csrftoken");
    const result = await axios.post(
      "https://my-first-pr.herokuapp.com/api/cart/",
      {
        first_name: firstName,
        address: address,
        last_name: lastName,
        phone: phone,
        email: email,
      },
      { withCredentials: true, headers: { "X-CSRFToken": csrftoken } }
    );
    return result;
  }
}

const test = new ApiManager();
