import {
  FETCHING_DATA,
  FETCHING_DATA_SUCCESS,
  FETCHING_DATA_FAIL,
  GET_PRODUCT_LIST,
  GET_CATEGORY_LIST,
  GET_SUBCATEGORY,
  GET_CART,
  ADD_ITEM_TO_CART,
  UPDATE_ITEM,
  SUCCESS_ITEM,
  SUCCESS_POSTING_ORDER,
  LOADING_POSTING,
  FAIL_POSTING_ORDER,
} from "../actions/actions";
import ApiManager from "../api";

const api = new ApiManager();

const findUniq = (arr) => {
  let names = [];
  arr.map((item) => names.push(item.name));
  let unique = names.filter((item, index) => names.indexOf(item) === index);
  let test = arr.filter((item) => {
    if (unique.findIndex((sub) => sub === item.name) > -1) {
      unique.splice(item.name, 1);
      return true;
    } else {
      return false;
    }
  });
  return test;
};

export const getProductList = (filter = "", name = "") => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    api
      .getProductList(filter, name)
      .then((result) => dispatch(GET_PRODUCT_LIST(result.data)))
      .then(() => dispatch(FETCHING_DATA_SUCCESS()))
      .catch((error) => dispatch(FETCHING_DATA_FAIL(error)));
  };
};

export const getCategoryList = () => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    api
      .getCategorylist()
      .then((result) => dispatch(GET_CATEGORY_LIST(result.data)))
      .then(() => dispatch(FETCHING_DATA_SUCCESS()))
      .catch((error) => dispatch(FETCHING_DATA_FAIL(error)));
  };
};

export const getSubCategoryList = (products) => {
  let pr = [];
  if (products) {
    products.map((item) => {
      item.subcategory.length > 1
        ? item.subcategory.map((sub) => pr.push(sub))
        : pr.push(item.subcategory[0]);
    });
  }
  const filteredsub = pr.length > 0 ? findUniq(pr) : [];
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    dispatch(GET_SUBCATEGORY(filteredsub));
    dispatch(FETCHING_DATA_SUCCESS());
  };
};

export const getCart = () => {
  return (dispatch) => {
    api.getCart().then((result) => dispatch(GET_CART(result.data)));
  };
};

export const addItemToCart = (id) => {
  return (dispatch) => {
    api
      .postItemToCart(id)
      .then((result) => dispatch(GET_CART(result.data)))
      .then(() => dispatch(ADD_ITEM_TO_CART()));
  };
};

export const UpdateCartItem = (id, quantity, action) => {
  return (dispatch) => {
    dispatch(UPDATE_ITEM());
    const update_amount = quantity + action;
    api
      .UpdateItemInCart(id, update_amount)
      .then(() => dispatch(SUCCESS_ITEM()));
  };
};

export const DeleteItemfromCart = (id) => {
  return (dispatch) => {
    dispatch(UPDATE_ITEM());
    api.DeleteItemInCart(id).then(() => dispatch(SUCCESS_ITEM()));
  };
};

export const ClearCart = (id) => {
  return (dispatch) => {
    dispatch(UPDATE_ITEM());
    api.clearCart(id).then(() => dispatch(SUCCESS_ITEM()));
  };
};

export const PostCart = (address, firstName, lastName, phone, email) => {
  return (dispatch) => {
    dispatch(UPDATE_ITEM());
    dispatch(LOADING_POSTING());
    api
      .postCart(address, firstName, lastName, phone, email)
      .then((data) => dispatch(SUCCESS_POSTING_ORDER(data.data["uuid"])))
      .then((_) => dispatch(SUCCESS_ITEM()))
      .catch((_) => FAIL_POSTING_ORDER());
  };
};
