import React from "react";
import ProductList from "../products/product-list";
import { CategoryPhone } from "../categories/category-list";
import { Route } from "react-router-dom";
import Cart from "../cart/cart";
import CartWidget from "../cart/cart-phone-widget";
import OrderForm from "../create-order/create-order-form";
import "./style.css";

const MainPagePhone = () => {
  return (
    <React.Fragment>
      <div className="custom-container">
        <div>
          <Route path="/:?" component={CategoryPhone} />
        </div>
        <div>
          <Route
            path="/products/:filter?/:name?"
            exact
            render={({ match }) => {
              const filter = match.params.filter;
              const name = match.params.name;
              return <ProductList filter={filter} name={name} />;
            }}
          />
        </div>
        <div>
          <Route path="/cart/" exact component={Cart} />
        </div>
        <CartWidget />
        <OrderForm />
      </div>
    </React.Fragment>
  );
};

export default MainPagePhone;
