import React from "react";
import searchicon from "./search.png";
import { useState } from "react";
import { withRouter } from "react-router-dom";

const Search = ({ pathParams, history }) => {
  let [inputFilter, setInput] = useState("");

  const OnSearch = () => {
    if (window.screen.width <= 600) {
      console.log("phone searching...");
      history.push(`/products/${pathParams}/name=${inputFilter}`);
    } else {
      history.push(`/${pathParams}/name=${inputFilter}`);
    }
  };

  return (
    <React.Fragment>
      <div className="search-form clearfix">
        <input
          className="form-control m-3 ml-3 search"
          placeholder="Поиск..."
          onChange={(e) => setInput(e.target.value)}
        />
        <div
          onClick={() => {
            OnSearch();
          }}
          className="search-button"
        >
          <img src={searchicon} className="search-icon" />
        </div>
      </div>
    </React.Fragment>
  );
};

export default withRouter(Search);
