import React from "react";
import { connect } from "react-redux";
import {
  getProductList,
  getSubCategoryList,
} from "../../service/thunk/thunkCreator";
import { withRouter } from "react-router-dom";
import { bindActionCreators } from "redux";
import { useEffect, useState } from "react";
import ProductItem from "./product-item";
import Search from "./search";

import "./style.css";

const ProductListView = ({ products, subcategory }) => {
  return (
    <div className="row product-block">
      {products.length === 0 ? (
        <div className="None-products">
          Извините, но продутов по такому фильтру не найдено
        </div>
      ) : (
        <div className="col-10 col-sm-4">
          {products.length === 1 ? (
            <div className="product-group">
              <div className="product-header">
                <h3>
                  {products[0].subcategory[0].name}
                  <span className="product-edit"></span>
                </h3>
              </div>
              <div className="row">
                <ProductItem product={products[0]} />
              </div>
            </div>
          ) : (
            subcategory.map((item, index) => (
              <div className="product-group" key={index}>
                <div className="product-header">
                  <h3>
                    {item.name}
                    <span className="product-edit">{item.count}</span>
                  </h3>
                </div>
                <div className="row">
                  {products
                    .filter((unfiltered) =>
                      unfiltered.subcategory
                        .map((sub) => sub.name)
                        .indexOf(item.name) > -1
                        ? true
                        : false
                    )
                    .map((product, index) => (
                      <ProductItem product={product} key={index} />
                    ))}
                </div>
              </div>
            ))
          )}
        </div>
      )}
    </div>
  );
};

const ProductList = ({
  products,
  getProductList,
  loading,
  getSubCategoryList,
  subcategory,
  filter,
  name,
}) => {
  useEffect(() => {
    getProductList(filter, name);
    return () => console.log("disable");
  }, [filter, getProductList, name]);

  useEffect(() => {
    getSubCategoryList(products);
  }, [products, getSubCategoryList]);

  return (
    <React.Fragment>
      <Search pathParams={filter} />
      {loading ? (
        <div className="spinner">
          <div className="spinner-border text-dark"></div>
        </div>
      ) : (
        <ProductListView products={products} subcategory={subcategory} />
      )}
    </React.Fragment>
  );
};

const putStateToProps = (state) => {
  return {
    products: state.ProductReducer.products,
    loading: state.GeneralReducer.loading,
    subcategory: state.ProductReducer.subcategory,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ getProductList, getSubCategoryList }, dispatch);
};

export default connect(
  putStateToProps,
  mapDispatchToProps
)(withRouter(ProductList));
