import React from "react";
import "./style.css";
import plus from "./plus-icon.png";
import info from "./info.png";
import { useState } from "react";
import { addItemToCart } from "../../service/thunk/thunkCreator";
import { connect } from "react-redux";

const ProductItem = ({ product, addItemToCart }) => {
  return (
    <div className="product col-xl-4 col-sm-7 col-lg-6 col-6 ">
      <div className="product-area">
        <img src={product.images[0].image} className="product-image" />
        <h6>{product.name}</h6>
        <span className="product-edit" style={{ fontSize: "16px" }}>
          {product.characteristics[0].value} {product.characteristics[0].suffix}
        </span>
        <div className="price-block">
          {product.price === product.price_with_discount ? (
            <h4>{product.price}P </h4>
          ) : (
            <div>
              <s>{product.price}P</s>
              <h4 className="price_with_discount">
                {product.price_with_discount}P
              </h4>
            </div>
          )}

          <div className="add-product">
            <img src={plus} onClick={() => addItemToCart(product.id)} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default connect(null, { addItemToCart })(ProductItem);
