const InitialState = {
  cart: [],
  total: 0,
  isAdded: false,
  isUpdate: false,
  isOpen: false,
  successPosting: false,
  loadingPosting: false,
  isWidgetOpen: false,
  cart_id: 0,
};

const CartReducer = (state = InitialState, action) => {
  switch (action.type) {
    case "GET_CART":
      return {
        ...state,
        cart: action.payload.cart,
        total: action.payload.total,
      };
    case "ADD_ITEM_TO_CART":
      return { ...state, isAdded: true, isWidgetOpen: true };
    case "CLOSE_WIDGET":
      return { ...state, isWidgetOpen: false };
    case "UPDATE_ITEM":
      return { ...state, isUpdate: true };
    case "SUCCESS_ITEM":
      return { ...state, isAdded: false, isUpdate: false };
    case "OPEN_MODAL":
      return { ...state, isOpen: true };
    case "CLOSE_MODAL":
      return { ...state, isOpen: false };
    case "SUCCESS_POSTING_ORDER":
      return {
        ...state,
        successPosting: true,
        loadingPosting: false,
        cart_id: action.payload,
      };
    case "FAIL_POSTING_ORDER":
      return { ...state, successPosting: false, loadingPosting: false };
    case "LOADING_POSTING":
      return { ...state, loadingPosting: true };
    default:
      return state;
  }
};

export default CartReducer;
