import React from "react";
import { connect } from "react-redux";
import { useEffect } from "react";
import { CLOSE_WIDGET } from "../../service/actions/actions";

const CartWidget = ({ cart, total, isWidgetOpen, CLOSE_WIDGET }) => {
  if (isWidgetOpen) {
    setTimeout(() => {
      CLOSE_WIDGET();
    }, 5000);
    return (
      <div className="cart-widget">
        Количество товаров в корзине: {cart.length}
        <h5>Итого: {total}</h5>
      </div>
    );
  }
  return null;
};

const mapStateToProps = (state) => {
  return {
    cart: state.CartReducer.cart,
    total: state.CartReducer.total,
    isWidgetOpen: state.CartReducer.isWidgetOpen,
  };
};

export default connect(mapStateToProps, { CLOSE_WIDGET })(CartWidget);
