import React from "react";
import MainPage from "./mainpage/mainpage";
import MainPagePhone from "./mainpage/mainpage-phone";
import { NavBar, MobileNavBar } from "./navbar/navbar";

const App = () => {
  if (window.screen.width <= 750) {
    return (
      <div className="page">
        <MobileNavBar />
        <MainPagePhone />
      </div>
    );
  }
  return (
    <div>
      <NavBar />
      <MainPage />
    </div>
  );
};

export default App;
