import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { CLOSE_MODAL } from "../../service/actions/actions";
import { required } from "../../service/validators/validators";
import { PostCart } from "../../service/thunk/thunkCreator";
import "./style.css";

let componentInput = ({
  input,
  type,
  placeholder,
  meta: { touched, error, warning },
}) => {
  return (
    <div>
      <input
        {...input}
        placeholder={placeholder}
        type={type}
        className="form-control"
      />
      {touched &&
        ((error && <span className="text-danger">{error}</span>) ||
          (warning && <span className="text-danger">{warning}</span>))}
    </div>
  );
};

let Inputs = () => {
  return (
    <React.Fragment>
      <div className="create-order-input">
        <Field
          name="firstName"
          component={componentInput}
          type="text"
          placeholder="First Name"
          className="form-control"
          validate={[required]}
        />
      </div>
      <div className="create-order-input">
        <Field
          name="lastName"
          component={componentInput}
          type="text"
          placeholder="Last Name"
          className="form-control"
          validate={[required]}
        />
      </div>
      <div className="create-order-input">
        <Field
          name="email"
          component={componentInput}
          type="email"
          placeholder="Email"
          className="form-control"
          validate={[required]}
        />
      </div>
      <div className="create-order-input">
        <Field
          component={componentInput}
          name="phone"
          type="number"
          placeholder="phone"
          className="form-control"
          validate={[required]}
        />
      </div>
      <div className="create-order-input">
        <Field
          name="address"
          component={componentInput}
          type="text"
          placeholder="address"
          className="form-control"
          validate={[required]}
        />
      </div>
    </React.Fragment>
  );
};

let OrderForm = (props) => {
  const onSubmit = (values) => {
    props.PostCart(
      values.address,
      values.firstName,
      values.lastName,
      values.phone,
      values.email
    );
  };

  const {
    isOpen,
    successPosting,
    CLOSE_MODAL,
    loadingPosting,
    cart_id,
  } = props;
  console.log(loadingPosting);

  if (isOpen) {
    return (
      <div className="form-wrapper">
        <form
          onSubmit={props.handleSubmit(onSubmit)}
          className="popup-form form-group"
        >
          <div className="form-header">
            <a className="close-form" onClick={() => CLOSE_MODAL()}>
              Закрыть
            </a>
            <h5>Введите данные для оформления заказа</h5>
          </div>
          {successPosting ? (
            <div>Заказ номер {cart_id} готов</div>
          ) : (
            <div>
              {!loadingPosting ? (
                <Inputs />
              ) : (
                <div className="spinner">
                  <div className="spinner-border text-dark"></div>
                </div>
              )}

              <button
                type="submit"
                className="btn btn-warning btn-lg btn-block"
              >
                Submit
              </button>
            </div>
          )}
        </form>
      </div>
    );
  } else {
    return null;
  }
};

OrderForm = reduxForm({
  form: "create-order",
})(OrderForm);

const mapStateToProps = (state) => {
  return {
    isOpen: state.CartReducer.isOpen,
    successPosting: state.CartReducer.successPosting,
    loadingPosting: state.CartReducer.loadingPosting,
    cart_id: state.CartReducer.cart_id,
  };
};

export default connect(mapStateToProps, { CLOSE_MODAL, PostCart })(OrderForm);
