import React from "react";
import { connect } from "react-redux";
import { useEffect } from "react";
import {
  UpdateCartItem,
  DeleteItemfromCart,
} from "../../service/thunk/thunkCreator";
import "./style.css";

const CartItem = ({ item, UpdateCartItem, DeleteItemfromCart }) => {
  return (
    <div className="cart-item">
      <div className="cart-item-title">
        <span>{item.product.name}</span>
      </div>
      <div className="cart-item-count">
        <div
          className="cart-item-inc"
          onClick={() => UpdateCartItem(item.product.id, item.quantity, 1)}
        >
          <span
            style={{
              height: "1rem",
            }}
          >
            +
          </span>
        </div>
        <span>{item.quantity}</span>
        <div
          className="cart-item-dec"
          onClick={() => UpdateCartItem(item.product.id, item.quantity, -1)}
        >
          <span
            style={{
              height: "1.1rem",
            }}
          >
            -
          </span>
        </div>
      </div>
      <div className="cart-item-price">
        <span>{item.total_price}P</span>
      </div>
      <div className="cart-item-delete">
        <button
          className="btn btn-danger"
          onClick={() => DeleteItemfromCart(item.product.id)}
        >
          X
        </button>
      </div>
    </div>
  );
};

export default connect(null, { UpdateCartItem, DeleteItemfromCart })(CartItem);
