import React from "react";
import ProductList from "../products/product-list";
import { CategoryDesktop } from "../categories/category-list";
import Cart from "../cart/cart";
import OrderForm from "../create-order/create-order-form";
import { Route } from "react-router-dom";
import "./style.css";

const MainPage = () => {
  return (
    <React.Fragment>
      <div className="custom-container">
        <div className="row">
          <div className="col-sm-3">
            <Route path=":?" component={CategoryDesktop} />
          </div>
          <div className="col-sm-6 p-0">
            <Route
              path="/:filter?/:name?"
              exact
              render={({ match }) => {
                const filter = match.params.filter;
                const name = match.params.name;
                return <ProductList filter={filter} name={name} />;
              }}
            />
          </div>
          <div className="col-sm-3">
            <Cart />
          </div>
        </div>
      </div>
      <OrderForm />
    </React.Fragment>
  );
};

export default MainPage;
